body {
	background: #ed583f;
}
.main-app {
	text-align: center;
	margin: 100px;
}
.text-color {
	color: #383f51;
	font-size: 22px;
}
.cta-button {
	padding: 5px;
	min-width: 200px;
	color: white;
	font-size: 18px;
	cursor: pointer;
}

.connect-wallet-button {
	background: rgb(32, 129, 226);
}

.mint-nft-button {
	margin: 20px;
	background: #f3a732;
	color: black;
	font-weight: normal;
	border-radius: 4px;
	border-style: solid;
	
}
.text-style {
	color: #000000;
	font-size: 14px;
}

div.card {
	box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.2),
		0 16px 20px 0 rgba(0, 0, 0, 0.19);
	text-align: center;
	max-width: 600px;
	max-height: 500px;
	margin: auto;
	margin-top: 50px;
	background: #f3e7d8;
	padding: 10px;
	border-radius: 4px;
	font-size: 14px;
}
div.header {
	background-color: #f3e7d8;
	color: #000000;
	padding: 0px;
	font-size: 18px;
	border-radius: 4px;
}

div.container {
	padding: 10px;
}
